body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100dvh;
  width: 100dvh;
}

#root {
	height: 100dvh;
	width: 100dvw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: #eee;
}

.main-container {
	position: relative;
	flex: none;
}
.game-outer {
	margin: 10px;
	padding: 40px;
}
.card {
	background-color: #fff;
	box-shadow: #0002 1px 3px 30px;
	border-radius: 10px;
}

.container {
	width: 100%;
	height: 100%;
	position: absolute;
}
.dot {
	position: absolute;
	margin: -5px 0 0 -5px;
	/*
	width: 4px;
	height: 4px;
	border: solid 3px black;
	*/
	background-color: black;
	width: 10px;
	height: 10px;
}

.line {
	position: absolute;
	background-color: #eee;
}
.line:hover {
	background-color: red;
}
.line.selected {
	background-color: #333;
	pointer-events: none;
}
.line.horizontal {
	width: 40px;
	height: 8px;
	margin-left: 5px;
	margin-top: -4px;
}
.line.vertical {
	height: 40px;
	width: 8px;
	margin-top: 5px;
	margin-left: -4px;
}
.line.selected.vertical {
	width: 6px;
	margin-left: -3px;
}
.line.selected.horizontal {
	height: 6px;
	margin-top: -3px;
}
.line.latest {
	background-color: blue;
}

.cell {
	position: absolute;
	width: 46px;
	height: 46px;
	margin: 2px 0 0 2px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.cell.selected {
	background-color: #f77;
}

.outer-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.people-list {
	margin: 1em;
	padding: 1em;
	background-color: #fff;
}
.person {
	display: flex;
	align-items: center;
	margin: 0.7em;
	padding: 0.3em;
	border: 1px solid #0000;
}
.person.turn {
	border: 1px solid #777;
}
.colorbox {
	height: 1em;
	width: 1em;
	margin-right: 0.5em;
}
.name {
	flex: 1;
}
.name.self::after {
	content: '(you)';
	margin-left: 0.5em;
	color: #0007;
}
.name.dead {
	font-style: italic;
	color: #aaa;
}
.score {
	margin-left: 2em;
}
.offline {
	position: absolute;
	top: 0; left: 0; right: 0; bottom: 0;
	background-color: #000e;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-size: 25px;
}
.tip {
	height: 1.5em;
	color: #0009;
	margin: 1em 0 1em 0;
}
.row {
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 1em;
}
.row input {
	flex: 1;
	margin: 0.8em 0 0 0;
}
.form {
	padding: 2em;
	display: flex;
	flex-direction: column;
	min-width: 25vw;
}
.form input[type="text"], .form button {
	width: auto;
	padding: 0.5em;
	font-size: 1em;
	margin: 0.8em 0 0 0;
	width: -webkit-fill-available;
	width: -moz-available;
}
.form label {
	margin: 0 0 2.5em 0;
}
.row .flex {
	flex: 1;
}
.option input[type="radio"] {
	display: none;
}
.option input[type="radio"] + div {
	padding: 0 1em;
	height: 4em;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	flex: 1;
	border: solid 2px #0005;
	border-radius: 5px;
	cursor: pointer;
}
.option input[type="radio"] + div:hover {
	transform: scale(1.02);
}
.option input[type="radio"]:disabled + div {
	color: #0004;
	border-color: #ddd;
	pointer-events: none;
}
.option input[type="radio"]:checked + div {
	background-color: #fec;
	border-color: #fa6;
}
.online-server-data {
	color: #000a;
	justify-content: space-between;
}
.online-server-data div {
	max-width: 20em;
	overflow: hidden;
	text-overflow: ellipsis;
	line-break: anywhere;
	word-wrap: break-word;
	line-height: 1.5em;
	max-height: 1.5em;
	display: block;
}
.row > .main {
	flex: 3;
}
.row > .sub {
	flex: none;
	width: fit-content;
}
